<template>
  <div class="update-name">
    <van-nav-bar title=""  left-arrow @click-left="$emit('click-close')">
      <template #right>
        <span class="text" @click="onSave"></span>
      </template>
    </van-nav-bar>
    <van-field
      v-model="name"
      rows="2"
      autosize
      label=""
      type="textarea"
      maxlength="10"
      placeholder=""
      show-word-limit
    />
  </div>
</template>

<script>
import { updateProfile } from '@/api/user'
export default {
  data () {
    return {
      name: ''
    }
  },
  props: {
    originalName: {
      type: String,
      required: true
    }
  },
  created () {
    this.name = this.originalName
  },
  methods: {
    async onSave () {
      try {
        await updateProfile({
          name: this.name
        })
        this.$emit('update:originalName', this.name)
        this.$emit('click-close')
        this.$toast.success('')
      } catch (err) {
        if (err.response) {
          this.$toast(', ')
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .text {
    color:white;
  }
</style>
