<template>
  <van-picker
    title=""
    show-toolbar
    :columns="columns"
    @confirm="onConfirm"
    @cancel="onCancel"
    :default-index="defaultIndex"
/>
</template>

<script>
import { updateProfile } from '@/api/user'
export default {
  data () {
    return {
      columns: ['', ''], // 0  , 1 
      defaultIndex: ''
    }
  },
  props: {
    originalGender: {
      type: Number,
      required: true
    }
  },
  created () {
    this.defaultIndex = this.originalGender
    console.log(this.defaultIndex)
  },
  methods: {
    async onConfirm (value, index) {
      this.$toast.loading({ // 
        message: '...',
        forbidClick: true,
        duration: 0
      })
      try {
        await updateProfile({
          gender: index
        })
        this.$emit('click-close')
        this.$emit('update:originalGender', index)
        this.$toast.success('')
      } catch (err) {
        if (err.response) {
          this.$toast.fail(', ')
        }
      }
    },
    onCancel () {
      this.$emit('click-close')
    }
  }
}
</script>

