<template>
  <van-datetime-picker
    v-model="currentDate"
    type="date"
    title=""
    :min-date="minDate"
    :max-date="maxDate"
    @confirm="confirm"
    @cancel="cancel"
/>
</template>

<script>
import { updateProfile } from '@/api/user'
import dayjs from 'dayjs'
export default {
  data () {
    return {
      minDate: new Date(1970, 0, 1),
      maxDate: new Date(),
      currentDate: new Date(this.value)
    }
  },
  props: {
    value: {
      type: String,
      required: true
    }
  },
  methods: {
    async confirm () { // 
      this.$toast.loading({ // 
        message: '...',
        forbidClick: true,
        duration: 0
      })
      try {
        await updateProfile({
          birthday: dayjs(this.currentDate).format('YYYY-MM-DD')
        })
        this.$emit('input', dayjs(this.currentDate).format('YYYY-MM-DD'))

        this.$toast.success('')

        this.$emit('click-close')
      } catch (err) {
        console.log(err)
        this.$toast.success(', ')
      }
    },
    cancel () { // 
      this.$emit('click-close')
    }
  }
}
</script>

