<template>
  <div class="user-profile">
    <van-nav-bar title="" left-arrow right-text="" @click-left="$router.back()"/>
    <van-cell-group>
      <!-- hidden -->
      <input type="file" hidden ref="file" @change="onFileChange">
      <van-cell title="" is-link @click="$refs.file.click()">
        <van-image
          round
          width="30"
          height="30"
          fit="cover"
          :src="profile.photo"
        />
      </van-cell>
      <van-cell title="" :value="profile.name" is-link @click="isUpdateNameShow = true"/>
      <van-cell title="" :value="profile.gender === 0 ? '' : ''" is-link @click="isUpdateGenderShow = true"/>
      <van-cell title="" :value="profile.birthday" is-link  @click="isUpdateBirthdayShow = true"/>
    </van-cell-group>
    <!--()-->
    <van-popup
      v-model="isUpdateNameShow"
      :style="{height:'100%'}"
      position="bottom"
      get-container="body"
      >
      <update-name :originalName.sync="profile.name" @click-close="isUpdateNameShow = false"/>
    </van-popup>
    <!--()-->
    <van-popup
      v-model="isUpdateGenderShow"
      :style="{height:'50%'}"
      position="bottom"
      get-container="body"
      >
      <update-gender :originalGender.sync="profile.gender" @click-close="isUpdateGenderShow = false"/>
    </van-popup>
    <!--()-->
    <van-popup
      v-model="isUpdateBirthdayShow"
      :style="{height:'50%'}"
      position="bottom"
      get-container="body"
      >
      <update-birthday v-if="isUpdateBirthdayShow" v-model="profile.birthday" @click-close="isUpdateBirthdayShow = false"/>
    </van-popup>
    <!--()-->
    <van-popup
      v-model="isUpdatePhotoShow"
      :style="{height:'100%'}"
      position="bottom"
      get-container="body"
      >
      <update-photo :img.sync="img" @click-close="isUpdatePhotoShow = false" v-if="isUpdatePhotoShow"></update-photo>
    </van-popup>
  </div>
</template>

<script>
import { getUserProfile } from '@/api/user'
import updateName from './components/update-name.vue'
import updateGender from './components/update-gender.vue'
import updateBirthday from './components/update-birthday.vue'
import updatePhoto from './components/update-photo.vue'
export default {
  name: 'UserProfile',
  components: {
    updateName,
    updateGender,
    updateBirthday,
    updatePhoto
  },
  data () {
    return {
      profile: {},
      isUpdateNameShow: false, //  /
      isUpdateGenderShow: false, //  /
      isUpdateBirthdayShow: false, // 
      isUpdatePhotoShow: false, // 
      img: null
    }
  },
  created () {
    this.getUserProfile()
  },
  methods: {
    async getUserProfile () {
      try {
        const { data } = await getUserProfile()
        this.profile = data
        console.log(this.profile)
      } catch (err) {
        console.log(err)
      }
    },
    onFileChange () { // file  change
      // 
      const file = this.$refs.file.files[0]
      //  blob 
      this.img = window.URL.createObjectURL(file)

      // 
      this.isUpdatePhotoShow = true

      // file-input  change 
      // ,  value 
      this.$refs.file.value = ''
    }
  }
}
</script>

<style scoped lang="less">
.user-profile {
  .van-cell__title {
      display: flex;
      align-items: center;
    }
  /deep/.avatar-cell {
      .van-cell__value {
        display: flex;
        flex-direction: row-reverse;
      }
      .avatar {
        width: 60px;
        height: 60px;
      }
    }
    /deep/.van-nav-bar__right {
      .van-nav-bar__text {
        color:white;
      }
    }
  }
</style>
